//
// ion-rangeslider.scss
//
.irs--flat{
  .irs-bar, .irs-to, .irs-from, .irs-single {
    background: $primary !important;
  }
  .irs-to, .irs-from, .irs-single{
    &:before{
      border-top-color: $primary;
    }
  }
  .irs-line{
    background-color: lighten($light, 4%);
  }
  
  .irs-grid-text{
    font-size: 12px;
  }
  .irs-handle {
    > i{
      &:first-child{
        background-color: $primary !important;
      }
    }
  }
}